<template>
  <div class="signup">
    <UnauthorizedHeader/>
    <div class="signup-header">
      <div class="title">Signup</div>
      <div class="description">  CommB   !</div>
    </div>
    <div class="account-form p-5 d-flex flex-column justify-content-center align-items-center">
      <img class="account-deco" src="@/assets/image/deco/accountDeco.svg" alt="accountDeco">
      
      <div class="account-inputs">
        <!--  input -->
        <div class="account-input-box">
          <div class="input-with-button">
            <input
              id="email"
              class="account-input"
              v-model="email"
              type="text"
              @input="resetEmailCheck"
              @keyup.enter="onSignup"
              autocapitalize="off"
              required
            />
              <button
                v-if="isChecked"
                class="btn-6 btn-primary1 email-check"
                disabled
              ></button>
              <button
                v-else
                :class="[ error.email ? 'btn-disabled' : 'btn-yellow', 'btn-6', 'email-check']"
                @click="checkEmail"
              ></button>
              <label> </label>
          </div>
          <div class="error-text" v-if="error.email">{{error.email}}</div>
        </div>
        <!--  input -->
        <div class="account-input-box">
          <input
            id="password"
            class="account-input"
            v-model="password"
            type="password"
            @keyup.enter="onSignup"
            required
          />
            <label></label>
          <div class="error-text" v-if="error.password">{{error.password}}</div>
        </div>
        <!--   input -->
        <div class="account-input-box">
          <input
            id="passwordConfirm"
            class="account-input"
            v-model="passwordConfirm"
            type="password"
            @keyup.enter="onSignup"
            required
          />
            <label> </label>
          <div class="error-text" v-if="error.passwordConfirm">{{error.passwordConfirm}}</div>
        </div>
        <!--  input -->
        <div class="account-input-box">
          <input
            id="name"
            class="account-input"
            v-model="name"
            type="text"
            @keyup.enter="onSignup"
            required
          />
            <label></label>
          <div class="error-text" v-if="error.name">{{error.name}}</div>
        </div>
        <!--   input -->
        <div class="account-input-box">
          <input
            id="nickname"
            class="account-input"
            v-model="nickname"
            type="text"
            maxlength="10"
            @keyup.enter="onSignup"
            required
          />
            <label></label>
          <div class="error-text" v-if="error.nickname">{{error.nickname}}</div>
        </div>
        <!--   -->
        <button
          :class="[ isChecked && isSubmit ? 'btn-yellow' : 'btn-disabled', 'btn-2', 'mt-4']"
          @click="onSignup(userInfo)"
        ></button>
      </div>
    </div>

    <!--     alert -->
    <div
      v-if="emailAlert"
      class="backdrop"
    ></div>
    <SignupEmailConfirmAlert
      v-if="emailAlert === 1"
      class="email-alert"
      data-bs-backdrop="static"
      tabindex="-1"
      aria-hidden="true"
      @ok="emailAlert=false"
    />
    <SignupEmailRejectAlert
      v-if="emailAlert === 2"
      class="email-alert"
      data-bs-backdrop="static"
      tabindex="-1"
      aria-hidden="true"
      @ok="emailAlert=false"
    />
  </div>
</template>

<script>
import userApi from '@/api/user'
import PV from "password-validator"
import * as EmailValidator from "email-validator"
import { mapActions } from "vuex"
import UnauthorizedHeader from '@/components/user/UnauthorizedHeader'
import SignupEmailConfirmAlert from '@/components/user/SignupEmailConfirmAlert'
import SignupEmailRejectAlert from '@/components/user/SignupEmailRejectAlert'

export default {
  name: 'Signup',
  components: {
    UnauthorizedHeader,
    SignupEmailConfirmAlert,
    SignupEmailRejectAlert
  },
  data: () => {
    return {
      email: '',
      password: '',
      passwordConfirm: '',
      name: '',
      nickname: '',
      error: {
        email: false,
        password: false,
        passwordConfirm: false,
        name: false,
        nickname: false,
      },
      isSubmit: false,
      isChecked: false,
      emailAlert: false,
      passwordSchema: new PV(),
    }
  },
  methods: {
    ...mapActions('user', ['onSignup']),
    //    
    async checkEmail () {
      await userApi.checkEmail(this.email)
        .then((res) => {
          this.emailAlert = 1
          this.isChecked = true
          console.log(res)
        })
        .catch((err) => {
          this.email = ''
          this.emailAlert = 2
          console.log(err)
        })
    },
    //     input  ,    
    resetEmailCheck () {
      if (this.isChecked === true) {
        this.isChecked = false
      }
    },
    //   
    checkForm() {
      //   
      if (this.email.length > 0 && !EmailValidator.validate(this.email)) {
        this.error.email = "  ."
      } else if (this.email.length === 0) {
        this.error.email = "  ."
      } else {
        this.error.email = false
      }
      //   
      if (
        this.password.length > 0 &&
        !this.passwordSchema.validate(this.password)
      ) {
        this.error.password = ",  8  ."
      } else if (this.password.length === 0) {
        this.error.password = "  ."
      } else {
        this.error.password = false
      }
      //   
      if (
        this.passwordConfirm.length > 0 &&
        !this.passwordSchema.validate(this.passwordConfirm)
      ) {
        this.error.passwordConfirm = ",  8  ."
      } else if (
        this.passwordConfirm != this.password
      ) {
        this.error.passwordConfirm = "  ."
      } else if (this.passwordConfirm.length === 0) {
        this.error.passwordConfirm = "    ."
      }  else {
        this.error.passwordConfirm = false}
      //   
      if (this.name.trim().length === 0) {
        this.error.name = " ."
      } else {
        this.error.name = false }
      //    (input  maxlength      )
      if (this.nickname.trim().length === 0) {
        this.error.nickname = " ."
      } else if (this.nickname.trim().length < 2) {
        this.error.nickname = " 2  10  ."
      } else {
        this.error.nickname = false}

      // submit   
      let isSubmit = true
      Object.values(this.error).map(v => {
        if (v) isSubmit = false
      })
      this.isSubmit = isSubmit
    },
  },
  created() {
    this.passwordSchema
      .is()
      .min(8)
      .is()
      .max(100)
      .has()
      .digits()
      .has()
      .letters();
  },
  watch: {
    email: function() {
      this.checkForm()
    },
    password: function() {
      this.checkForm()
    },
    passwordConfirm: function() {
      this.checkForm()
    },
    name: function() {
      this.checkForm()
    },
    nickname: function() {
      this.checkForm()
    },
  },
  computed: {
    userInfo: function () {
      return {
        'email': this.email,
        'name': this.name,
        'nickname': this.nickname,
        'password': this.password
      }
    },
  },
}
</script>

<style scoped>
  .signup {
    display: flex;
    flex-flow: column;
    height: 100%;
    min-height: 100vh;
  }
  .backdrop {
    position: fixed;
    width: 100%;
    height: 100vh;
    z-index: 1040;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .signup-header {
    margin: 65px 20px 20px;
    flex: 0;
  }
  .signup-header .title {
    font-family: 'Black Han Sans', sans-serif;
    font-size: 2.5rem;
    color: #fff;
    text-shadow: 2px 2px #683EC9;
  }
  .signup-header .description {
    color: #fff;
    font-weight: 300;
    font-size: 0.7rem;
  }
  .input-with-button {
    position: relative;
  }
  .email-check {
    position: absolute;
    bottom: 7px;
    right: 10px;
  }
  .email-alert {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1060;
    transform: translate(-50%, -50%);
  }
</style>