<template>
  <div class="find-password">
    <UnauthorizedHeader/>
    <div class="fp-header">
      <div class="title">Reset<br>Password</div>
    </div>
    <div class="account-form d-flex flex-column align-items-center">
      <img class="account-deco" src="@/assets/image/deco/accountDeco.svg" alt="accountDeco">
      <div class="description">
          !<br>
          .
      </div>
      <div class="account-inputs">
        <!--   input -->
        <div class="account-input-box">
          <input
            id="password"
            class="account-input"
            v-model="password"
            type="password"
            required
          />
            <label> </label>
          <div class="error-text" v-if="error.password">{{error.password}}</div>
        </div>
        <!--    input -->
        <div class="account-input-box">
          <input
            id="passwordConfirm"
            class="account-input"
            v-model="passwordConfirm"
            type="password"
            required
          />
            <label>  </label>
          <div class="error-text" v-if="error.passwordConfirm">{{error.passwordConfirm}}</div>
        </div>
        <button
          :class="[ isSubmit ? 'btn-yellow' : 'btn-disabled', 'btn-2', 'mt-4']"
          @click="onResetPassword(userInfo)"
        > </button>
      </div>
    </div>
  </div>
</template>

<script>
import PV from "password-validator"
import { mapActions } from 'vuex'

import UnauthorizedHeader from '@/components/user/UnauthorizedHeader'
export default {
  name: 'ResetPassword',
  components: {
    UnauthorizedHeader
  },
  data () {
    return{
      key: null,
      password: '',
      passwordConfirm: '',
      error: {
        password: false,
        passwordConfirm: false,
      },
      isSubmit: false,
      passwordSchema: new PV(),
    }
  },
  methods: {
    ...mapActions('user', ['onResetPassword']),
    checkForm() {
      //    
      if (
        this.password.length > 0 &&
        !this.passwordSchema.validate(this.password)
      ) {
        this.error.password = ",  8  ."
      } else if (this.password.length === 0) {
        this.error.password = "  ."
      } else {
        this.error.password = false
      }

      //   
      if (
        this.passwordConfirm.length > 0 &&
        !this.passwordSchema.validate(this.passwordConfirm)
      ) {
        this.error.passwordConfirm = ",  8  ."
      } else if (
        this.passwordConfirm != this.password
      ) {
        this.error.passwordConfirm = "  ."
      } else if (this.passwordConfirm.length === 0) {
        this.error.passwordConfirm = "    ."
      }  else {
        this.error.passwordConfirm = false
      }

      let isSubmit = true;
      Object.values(this.error).map(v => {
        if (v) isSubmit = false
      });
      this.isSubmit = isSubmit
    },
  },
  created () {
    this.key = this.$route.query.key
    this.passwordSchema
      .is()
      .min(8)
      .is()
      .max(100)
      .has()
      .digits()
      .has()
      .letters();
  },
  watch: {
    password: function() {
      this.checkForm()
    },
    passwordConfirm: function() {
      this.checkForm()
    },
  },
  computed: {
    userInfo () {
      return {
        'key': this.key,
        'password': this.password
      }
    }
  }
}
</script>

<style scoped>
  .find-password {
    display: flex;
    flex-flow: column;
    height: 100%;
    min-height: 100vh;
  }
  .fp-header {
    margin: 65px 20px 20px;
    flex: 0;
  }
  .fp-header .title {
    font-family: 'Black Han Sans', sans-serif;
    font-size: 2.5rem;
    line-height: 3rem;
    color: #fff;
    text-shadow: 2px 2px #683EC9;
  }
  .account-form .description {
    color: #212121;
    font-size: 11px;
    margin: 60px 0 15px;
  }
</style>