<template>
  <div class="login">
    <UnauthorizedHeader/>
    <!--  -->
    <div
      v-if="needEmailConfirm"
      class="backdrop"
    ></div>
    <EmailReConfirmAlert
      v-if="needEmailConfirm"
      class="email-alert"
      data-bs-backdrop="static"
      tabindex="-1"
      aria-hidden="true"
      :email="email"
      @ok="closeAlert"
    />
    <div class="login-header">
      <div :class="[ wrongInput ? 'show' : 'hide', 'wrong-input']" role="alert">
           .
      </div>
      <div class="title">Login</div>
      <div class="description">!   CommB   !</div>
    </div>
    <div class="account-form p-5 d-flex flex-column justify-content-center align-items-center">
      <img class="account-deco" src="@/assets/image/deco/accountDeco.svg" alt="accountDeco">
      <div class="account-inputs">
        <!--  input -->
        <div class="account-input-box">
          <input
            id="email"
            class="account-input"
            v-model="email"
            type="text"
            @keyup.enter="login"
            autocapitalize="off"
            required
          />
            <label> </label>
          <div class="error-text" v-if="error.email">{{error.email}}</div>
        </div>
        <!--  input -->
        <div class="account-input-box mt-2">
          <input
            id="password"
            class="account-input"
            v-model="password"
            type="password"
            @keyup.enter="login"
            required
          />
            <label></label>
          <div class="error-text" v-if="error.password">{{error.password}}</div>
        </div>
        <!--   -->
        <button
          :class="[ isSubmit ? 'btn-yellow' : 'btn-disabled', 'btn-2', 'mt-4']"
          @click="login"
        ></button>
      </div>

      <div class="move-btns my-2">
        <span
          class="btn-text-s"
          type="button"
          @click="moveToSignup"
        ></span>
        <span class="btn-text-s"> | </span>
        <span
          class="btn-text-s"
          type="button"
          @click="$router.push({ name: 'FindPassword'})"
        > </span>
      </div>

      <div class="social-login">
        <img class="img-fluid my-3" src="@/assets/image/deco/or.svg" alt="">
        <div class="d-flex gap-3 justify-content-center">
          <button class="kakao-btn">
            <img src="@/assets/image/deco/kakao.png" alt="">
          </button>
          <button class="google-btn">
            <img src="@/assets/image/deco/google.png" alt="">
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PV from "password-validator"
import * as EmailValidator from "email-validator"
import { mapActions } from "vuex"
import UnauthorizedHeader from '@/components/user/UnauthorizedHeader'
import EmailReConfirmAlert from '@/components/user/EmailReConfirmAlert'

export default {
  name: 'Login',
  components: {
    UnauthorizedHeader,
    EmailReConfirmAlert
  },
  data: () => {
    return {
      email: '',
      password: '',
      passwordSchema: new PV(),
      error: {
        email: false,
        passowrd: false
      },
      isSubmit: false,
      needEmailConfirm: false,
      wrongInput: false
    }
  },
  methods: {
    ...mapActions('user', ['moveToSignup', 'onLogin']),
    closeAlert () {
      this.needEmailConfirm = false
    },
    async login () {
      await this.onLogin(this.userData)
        .catch ((err) => {
          console.log(err)
          if (err.status === 403) {
            this.needEmailConfirm = true
            this.hasAlert = true
          } else if (err.status === 401) {
            this.wrongInput = true
            setTimeout(() => {
              this.wrongInput = false
            }, 2000)
          }
        })
    },
    checkForm() {
      //   
      if (this.email.length >= 0 && !EmailValidator.validate(this.email)) {
        this.error.email = "  ."
      } else {
        this.error.email = false
      }
      //   
      if (
        this.password.length >= 0 &&
        !this.passwordSchema.validate(this.password)
      ) {
        this.error.password = ",   8  ."
      } else {
        this.error.password = false
      }

      // submit   
      let isSubmit = true;
      Object.values(this.error).map(v => {
        if (v) isSubmit = false;
      });
      this.isSubmit = isSubmit;
    },
  },
  created() {
    this.passwordSchema
      .is()
      .min(8)
      .is()
      .max(100)
      .has()
      .digits()
      .has()
      .letters();
  },
  watch: {
    email: function() {
      this.checkForm();
    },
    password: function() {
      this.checkForm();
    }
  },
  computed: {
    userData: function () {
      return {
        'email': this.email,
        'password': this.password
      }
    },
  },
}
</script>

<style scoped>
  .login {
    display: flex;
    flex-flow: column;
    height: 100%;
    min-height: 100vh;
  }
  .login-header {
    margin: 65px 20px 20px;
    flex: 0;
  }
  .login-header .title {
    font-family: 'Black Han Sans', sans-serif;
    font-size: 2.5rem;
    color: #fff;
    text-shadow: 2px 2px #683EC9;
  }
  .login-header .description {
    color: #fff;
    font-weight: 300;
    font-size: 0.7rem;
  }

  .social-login {
    width: 210px;
  }

  .social-login button {
    width: 45px;
    height: 45px;
    border-radius: 100%;
    border: none;
    outline: none;
  }

  .kakao-btn {
    background-color: #FFD600;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }

  .google-btn {
    background-color: #fff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }
  
  .social-login button img {
    max-width: 70%;
    height: auto;
  }

  .email-check {
    position: absolute;
    bottom: 7px;
    right: 10px;
  }
  .email-alert {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1060;
    transform: translate(-50%, -50%);
  }
  .backdrop {
    position: fixed;
    width: 100%;
    height: 100vh;
    z-index: 1040;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .wrong-input {
    width: 80vw;
    height: 35px;
    border-radius: 10px;
    font-size: 0.85rem;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 119, 119, 0.95);
    border: 1px solid #FF7777;
    color: #fff;
    position: fixed;
    left: 50%;
    transform: translate(-50%);
  }
  .show {
    opacity: 1;
    transition: all 0.2s;
  }
  .hide {
    opacity: 0;
    transition: all 0.2s;
  }
</style>