<template>
  <div class="update-info">
    <div class="info-header">
      <div class="title">My Info</div>
      <div class="description">  ,    .</div>
    </div>
    <div class="account-form p-5 d-flex flex-column align-items-center">
      <div :class="[ fail.password ? 'show' : 'hide', 'fail-alert', 'my-1']" role="alert">
           .
      </div>
      <img class="account-deco" src="@/assets/image/deco/accountDeco.svg" alt="accountDeco">
      <div class="d-flex flex-column align-items-center">
        <!--   preview -->
        <img v-if="preview" class="profile" :src="preview" alt="">
        <!--    -->
        <img v-else-if="profilePath" class="profile" :src="profilePath"  alt="">
        <!--      -->
        <img v-else class="profile" src="@/assets/image/common/profileDefault.svg" alt="">
        
        <div class="btn-text-primary mt-1">
          <span
            type="button" 
            data-bs-toggle="modal" 
            data-bs-target="#exampleModal"
          > </span>
          <ProfileCrop
            @select-croppa="saveNewProfile"
          />
          <span class="mx-1"></span>
          <span
            type="button"
            @click="onFileDelete"
          > </span>
        </div>
      </div>
      <div class="account-inputs">
        <!--  input -->
        <div class="account-input-box">
          <input
            id="nickname"
            class="account-input"
            v-model="nickname"
            type="text"
            autocapitalize="off"
            maxlength="10"
            required
          />
            <label></label>
          <div class="error-text" v-if="error.nickname">{{error.nickname}}</div>
        </div>

        <div 
          class="btn-text-primary mt-1 text-center mt-3"
          type="button"
          @click="passwordToggle"
        > </div>
        <div 
          v-if="updatePassword"
        >
          <!--   input -->
          <div class="account-input-box">
            <input
              id="oldPassword"
              class="account-input"
              v-model="oldPassword"
              type="password"
              required
            />
              <label> </label>
            <div class="error-text" v-if="error.oldPassword">{{error.oldPassword}}</div>
          </div>
          <!--   input -->
          <div class="account-input-box">
            <input
              id="password"
              class="account-input"
              v-model="password"
              type="password"
              required
            />
              <label> </label>
            <div class="error-text" v-if="error.password">{{error.password}}</div>
          </div>
          <!--    input -->
          <div class="account-input-box">
            <input
              id="passwordConfirm"
              class="account-input"
              v-model="passwordConfirm"
              type="password"
              required
            />
              <label>  </label>
            <div class="error-text" v-if="error.passwordConfirm">{{error.passwordConfirm}}</div>
          </div>
        </div>

        <!--   /   -->
        <div class="submit-btns d-flex justify-content-center gap-3">
          <button
            class="btn-5 btn-grey mt-4"
            @click="$router.go(-1)"
          ></button>
          <button
            :class="[ isSubmit ? 'btn-yellow' : 'btn-disabled', 'btn-5', 'mt-4']"
            @click="onUpdate"
          > </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PV from "password-validator"
import ProfileCrop from '@/components/user/ProfileCrop'
import { mapState, mapActions } from "vuex"
import userApi from '@/api/user'

export default {
  name: 'UpdateInfo',
  components: {
    ProfileCrop
  },
  data: () => {
    return {
      myCroppa: null,
      //  
      profilePath: null,
      preview: null,
      profileUpdate: 0,
      //  
      nickname: '',
      //  
      oldPassword: '',
      password: '',
      passwordConfirm: '',
      error: {
        nickname: false,
        oldPassword: false,
        password: false,
        passwordConfirm: false,
      },
      isSubmit: false,
      passwordSchema: new PV(),
      updatePassword: false,
      fail: {
        userInfo: false,
        password: false
      },
      isSuccessInfo: false,
      isSuccessPassword: false
    }
  },
  methods: {
    ...mapActions('user', ['onUpdatePassword']),
    passwordToggle() {
      this.updatePassword = !this.updatePassword
      this.checkForm()
    },
    saveNewProfile (croppa) {
      this.preview = croppa.generateDataUrl('image/jpeg')
      this.myCroppa = croppa
      this.profileUpdate = 1
    },
    onFileDelete () {
      this.profileUpdate = 2
      this.preview = null
      this.profilePath = null
      this.myCroppa = null
    },
    onUpdate () {
      //        
      if (this.profileUpdate != 0 || this.nickname != this.myInfo.nickname) {
        //    , blob 
        if (this.profileUpdate === 1) {
          this.myCroppa.generateBlob((blob) => {
            var userInfo = new FormData()
            userInfo.append('userFileUrl', blob, `${this.myInfo.id}.png`)
            userInfo.append('nickname', this.nickname)
            userInfo.append('flag', this.profileUpdate)
  
            userApi.updateInfo(this.myInfo.id, userInfo)
              .then((res) => {
                console.log(res.data)
                this.$store.commit('user/SET_MY_INFO', res.data.data)
                this.isSuccessInfo = true
              })
              .catch((err) => {
                console.log(err.response)

              })
          })
        } else {
          //   / 
          var userInfo = new FormData()
            // userInfo.append('userFileUrl', blob, `${this.myInfo.id}.png`)
            userInfo.append('nickname', this.nickname)
            userInfo.append('flag', this.profileUpdate)

          userApi.updateInfo(this.myInfo.id, userInfo)
            .then((res) => {
              console.log(res.data)
              this.isSuccessInfo = true
              this.$store.commit('user/SET_MY_INFO', res.data.data)
            })
            .catch((err) => {
              console.log(err.response)
            })
        }
      } else {
        this.isSuccessInfo = true
      }
      //     
      if (this.updatePassword) {
        const passwordInfo = {
          'newPassword': this.password,
          'oldPassword': this.oldPassword
        }
        this.onUpdatePassword(passwordInfo)
          .then(() => {
            this.isSuccessPassword = true
          })
          .catch(() => {
            this.fail.password = true
            setTimeout(() => {
              this.fail.password = false
            }, 2000)
          })
      } else {
        this.isSuccessPassword = true
      }
    },
    completeUpdate() {
      if (this.isSuccessInfo && this.isSuccessPassword) {
        this.$router.go(-1)
      }
    },
    checkForm() {
      if (this.nickname.trim().length === 0) {
        this.error.nickname = " .";
      } else if (this.nickname.trim().length < 2) {
        this.error.nickname = " 2  10  ."
      } else {
        this.error.nickname = false;
      }
      //    
      if (
        this.oldPassword.length > 0 &&
        !this.passwordSchema.validate(this.oldPassword)
      ) {
        this.error.oldPassword = ",  8  ."
      } else if (this.oldPassword.length === 0) {
        this.error.oldPassword = "  ."
      } else {
        this.error.oldPassword = false
      }
      //    
      if (
        this.password.length > 0 &&
        !this.passwordSchema.validate(this.password)
      ) {
        this.error.password = ",  8  ."
      } else if (this.password.length === 0) {
        this.error.password = "  ."
      } else if (
        this.password.length > 0 && 
        this.password === this.oldPassword) {
        this.error.password = "  ."
      } else {
        this.error.password = false
      }

      //   
      if (
        this.passwordConfirm.length > 0 &&
        !this.passwordSchema.validate(this.passwordConfirm)
      ) {
        this.error.passwordConfirm = ",  8  ."
      } else if (
        this.passwordConfirm != this.password
      ) {
        this.error.passwordConfirm = "  ."
      } else if (this.passwordConfirm.length === 0) {
        this.error.passwordConfirm = "    ."
      }  else {
        this.error.passwordConfirm = false
      }

      if (this.updatePassword) {
        let isSubmit = true;
        Object.values(this.error).map(v => {
          if (v) isSubmit = false
        });
        this.isSubmit = isSubmit
      } else {
        if (!this.error.nickname) {
          this.isSubmit = true
        }
      }
    },
  },
  created() {
    this.passwordSchema
      .is()
      .min(8)
      .is()
      .max(100)
      .has()
      .digits()
      .has()
      .letters();
    
    this.profilePath = this.myInfo.userFileUrl
    this.nickname = this.myInfo.nickname
  },
  watch: {
    nickname: function() {
      this.checkForm()
    },
    oldPassword: function() {
      this.checkForm()
    },
    password: function() {
      this.checkForm()
    },
    passwordConfirm: function() {
      this.checkForm()
    },
    isSuccessInfo: function () {
      this.completeUpdate()
    },
    isSuccessPassword: function () {
      this.completeUpdate()
    }

  },
  computed: {
    ...mapState('user', ['myInfo', 'accessToken']),
  }
}
</script>

<style scoped>
  .update-info {
    display: flex;
    flex-flow: column;
    height: 100%;
    min-height: 100vh;
  }
  .info-header {
    margin: 65px 20px 20px;
    flex: 0;
  }
  .info-header .title {
    font-family: 'Black Han Sans', sans-serif;
    font-size: 2.5rem;
    color: #fff;
    text-shadow: 2px 2px #683EC9;
  }
  .info-header .description {
    color: #fff;
    font-weight: 300;
    font-size: 0.7rem;
  }

  .profile {
    width: 80px;
    height: 80px;
    border-radius: 100%;
  }
  .fail-alert {
    width: 80vw;
    height: 35px;
    border-radius: 10px;
    font-size: 0.85rem;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 119, 119, 0.95);
    border: 1px solid #FF7777;
    color: #fff;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 1060;
  }
  .show {
    opacity: 1;
    transition: all 0.2s;
  }
  .hide {
    opacity: 0;
    display: none;
    transition: all 0.2s;
  }
</style>