<template>
  <div class="find-password">
    <UnauthorizedHeader/>
    <div class="fp-header">
      <div class="title">Reset<br>Password</div>
    </div>
    <div class="account-form d-flex flex-column align-items-center">
      <img class="account-deco" src="@/assets/image/deco/accountDeco.svg" alt="accountDeco">
      <div class="description">
          ,    ! <br>
             , <br>
           . 
      </div>
      <div class="account-inputs">
        <!--  input -->
        <div class="account-input-box">
          <input
            id="email"
            class="account-input"
            v-model="email"
            type="text"
            @keyup.enter="login"
            autocapitalize="off"
            required
          />
            <label> </label>
          <div class="error-text" v-if="error.email">{{error.email}}</div>
        </div>
        <button
          :class="[ isSubmit ? 'btn-yellow' : 'btn-disabled', 'btn-2', 'mt-4']"
        >   </button>
      </div>
    </div>
  </div>
</template>

<script>
import * as EmailValidator from "email-validator"
import UnauthorizedHeader from '@/components/user/UnauthorizedHeader'

export default {
  name: 'FindPassword',
  components: {
    UnauthorizedHeader,
  },
  data: () => {
    return {
      email: '',
      error: {
        email: false,
      },
      isSubmit: false,
    }
  },
  methods: {
    checkForm() {
      //   
            if (this.email.length > 0 && !EmailValidator.validate(this.email)) {
        this.error.email = "  ."
        this.isSubmit = false
      } else if (this.email.length === 0) {
        this.error.email = "  ."
        this.isSubmit = false
      } else {
        this.error.email = false
        this.isSubmit = true
      }
    }
  },
  watch: {
    email: function() {
      this.checkForm();
    },
  },
}
</script>

<style scoped>
  .find-password {
    display: flex;
    flex-flow: column;
    height: 100%;
    min-height: 100vh;
  }
  .fp-header {
    margin: 65px 20px 20px;
    flex: 0;
  }
  .fp-header .title {
    font-family: 'Black Han Sans', sans-serif;
    font-size: 2.5rem;
    line-height: 3rem;
    color: #fff;
    text-shadow: 2px 2px #683EC9;
  }
  .account-form .description {
    color: #212121;
    font-size: 11px;
    margin: 60px 0 15px;
  }
</style>